@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

  
  
  *{
   scroll-behaviour:smooth;
 }

 .App{
  min-height: 100vh;
  /* width:min-content; */
  background-color: #040009;
  color: rgba(221, 230, 237, 0.905);
  padding-top: 130px;
padding-bottom: 70px;
  -webkit-animation: example 40s linear infinite;
          animation: example 40s linear infinite;
  -webkit-animation-delay:1s;
          animation-delay:1s;
 }
 @-webkit-keyframes example{
   0%{background-color: rgb(223, 98, 15);}
   10%{background-color: rgb(41, 61, 2);}
   20%{background-color: rgb(4, 242, 190);}
   30%{background-color: rgb(68, 8, 187);}
   40%{background-color: rgb(10, 25, 63);}
   50%{background-color: blanchedalmond;}
   60%{background-color: rgb(173, 14, 213);}
   75%{background-color: rgb(104, 69, 18);}
   80%{background-color: rgb(36, 6, 85);}
   90%{background-color: rgb(255, 17, 215);}
   100%{background-color: rgb(51, 214, 193);}
 }
 @keyframes example{
   0%{background-color: rgb(223, 98, 15);}
   10%{background-color: rgb(41, 61, 2);}
   20%{background-color: rgb(4, 242, 190);}
   30%{background-color: rgb(68, 8, 187);}
   40%{background-color: rgb(10, 25, 63);}
   50%{background-color: blanchedalmond;}
   60%{background-color: rgb(173, 14, 213);}
   75%{background-color: rgb(104, 69, 18);}
   80%{background-color: rgb(36, 6, 85);}
   90%{background-color: rgb(255, 17, 215);}
   100%{background-color: rgb(51, 214, 193);}
 }
 .App:before{
     content: "";
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 600vh;
                background-image: url(
https://media.geeksforgeeks.org/wp-content/uploads/20200828184719/rain-300x300.png);
                -webkit-animation: rain 0.4s linear infinite;
                        animation: rain 0.4s linear infinite;
                opacity: 0;
 }


  @-webkit-keyframes rain {
                0% {
                    background-position: 0 0;
                    opacity: 1;
                }
  
                100% {
                    background-position: 8% 80%;
                    opacity: 1;
                }
            }


  @keyframes rain {
                0% {
                    background-position: 0 0;
                    opacity: 1;
                }
  
                100% {
                    background-position: 8% 80%;
                    opacity: 1;
                }
            }

            @-webkit-keyframes color-change {
                0% {
                    -webkit-filter: hue-rotate(0deg);
                            filter: hue-rotate(0deg);
                }
                50% {
                    -webkit-filter: hue-rotate(0deg);
                            filter: hue-rotate(0deg);
                }
                100% {
                    -webkit-filter: hue-rotate(360deg);
                            filter: hue-rotate(360deg);
                }
            }

            @keyframes color-change {
                0% {
                    -webkit-filter: hue-rotate(0deg);
                            filter: hue-rotate(0deg);
                }
                50% {
                    -webkit-filter: hue-rotate(0deg);
                            filter: hue-rotate(0deg);
                }
                100% {
                    -webkit-filter: hue-rotate(360deg);
                            filter: hue-rotate(360deg);
                }
            }




 @media(max-width:700px){
   .App{
     padding-top: 70px;
   }
 }

 .pageTitle{
   text-transform: uppercase;
   display: -webkit-flex;
   display: flex;
   -webkit-justify-content: center;
           justify-content: center;
   font-family: 'Monteserrat', 'sans-serif';
   font-size: 2vw;
   padding: 4px;
   border-radius: 50px;
   color: wheat;

 }
 @media(max-width:1000px){
   .pageTitle{
     font-size:6.4vw;
   }
 }



.media {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 200px;
  padding: 5px;
  margin: 5px 0;
  background-color: #282c34;
  border-radius: 10px;
  position: relative;
  font-family: "Montserrat", sans-serif;
}

.media:hover {
  background-color: rgb(109, 11, 247);
  color: 'darkslateblue';
}

@media (max-width: 550px) {
  .media {
    width: 46%;
  }
}

.poster {
  border-radius: 10px;
}

.title {
  width: 100%;
  text-align: center;
  font-size: 17px;
  padding: 8px 0;
}

.subTitle {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding-bottom: 3px;
  padding: 0 2px;
}

.content_lands {
  object-fit: contain;
  border-radius: 10px;
}

.content_porait {
  display: none;
  object-fit: contain;
  border-radius: 10px;
}

.tagline {
  padding-bottom: 10px;
  -webkit-align-self: center;
          align-self: center;
}

.contentM {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  height: 100%;
  width: 100%;
  overflow-y: scroll;
  scrollbar-width: none;
}

.contentM::-webkit-scrollbar {
  display: none;
}

.contentMA {
  padding: 10px;
  width: 95%;
  height: 90%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  font-family: "Roboto", sans-serif;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  font-weight: 300;
}

.contentMT {
  height: 12%;
  font-size: 5vw;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.contentMD {
  display: -webkit-flex;
  display: flex;
  height: 40%;
  overflow-y: scroll;
  padding: 15px;
  border-radius: 20px;
  scrollbar-width: thin; /* Firefox */
  box-shadow: inset 0 0 5px #000000;
  text-align: justify;
}

.contentMD::-webkit-scrollbar {
  display: none;
}

@media (min-width: 835px) {
  .content_lands {
    display: none;
  }
  .content_porait {
    display: -webkit-flex;
    display: flex;
    width: 38%;
  }
  .contentM {
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    padding: 10px 0;
  }
  .contentMA {
    width: 58%;
    padding: 0;
    height: 100%;
  }
  .contentMT {
    font-size: 3.5vw;
  }
  .contentMD {
    font-size: 22px;
  }
}
.trending{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-around;
          justify-content: space-around;

}

